body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  font-size: 1em;
  padding: 4vh;
  background-color: #E6E6E2;
  color: #1F1C2C;
  font-family: 'Crimson Text', serif;
}

hr {
  border: 0;
  height: 1px;
  background-image: -webkit-linear-gradient(left, #E6E6E2, #1F1C2C, #E6E6E2);
  background-image: linear-gradient(to right, #E6E6E2, #1F1C2C, #E6E6E2);
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.8em;
}

h3 {
  font-size: 1.5em;
}

h4 {
  font-size: 1em;
}

p {
  display: flex;
}
.header {
  flex-direction: row;
  flex-flow: row; 
  display:flex;
  height: 80px;
  margin-bottom: 20px;
}

.linked-in-button {
  margin-top: 32px;
  margin-left: 10px;
  width: 32px;

  -webkit-transition: -webkit-filter .5s ease-in-out;

  transition: -webkit-filter .5s ease-in-out;

  transition: filter .5s ease-in-out;

  transition: filter .5s ease-in-out, -webkit-filter .5s ease-in-out;

  -webkit-filter: grayscale(100%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
  filter: grayscale(100%); /* FF 35+ */
}

.linked-in-button:hover {
  -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
  filter: grayscale(0%); /* FF 35+ */
}
